<template>
  <div>
    <input type="text" placeholder="" v-model="username" @input="input" class="user"/>
    <input type="text" placeholder="" v-model="telnum" @input="input" />
    <input type="password" placeholder="" v-model="password" @input="input" />
    <input
      type="password"
      placeholder=""
      v-model="psconfirm"
      @input="input"
      @change="handlePsChange"
      class="confirm"
    />
    <p class="tips" v-if="showtips">
      <svg class="icon" aria-hidden="true">
        <use xlink:href="#icon-warning" />
      </svg>
      
    </p>
    <a href="/#/home" class="home"></a>
    <a href="/#/login" class="login"></a>
    <button :style="{background:buttonColor}" @click="handleClick()"></button>
    <p class="footer">--  --</p>
  </div>
</template>

<script>
import Axios from 'axios'
import { MessageBox, Toast } from 'mint-ui'
export default {
  data () {
    return {
      username: '',
      telnum: '',
      password: '',
      psconfirm: '',
      buttonColor: '#ccc',
      showtips: false
    }
  },
  methods: {
    input () {
      if (
        this.username !== '' &&
        this.password !== '' &&
        this.telnum !== '' &&
        this.psconfirm !== ''
      ) {
        this.buttonColor = '#ffd100'
      } else {
        this.buttonColor = '#ccc'
      }
    },
    handleTelchange () {
      Axios.post('/register', {
        tel: this.telnum
      }).then(res => {
        if (res.data.data === 0) {
          alert('')
        } else {
          console.log('')
        }
      })
    },
    handlePsChange () {
      if (this.password !== this.psconfirm) {
        this.showtips = true
      } else {
        this.showtips = false
      }
    },
    handleClick () {
      Axios.post('/register', {
        username: this.username,
        password: this.password,
        tel: this.telnum
      }).then(res => {
        if (res.data.data === '!') {
          Toast({
            message: '',
            position: 'middle',
            duration: 5000
          })
          this.$router.push('/login')
        } else {
          MessageBox('', '!')
        }
      })
    }
  }
}
</script>

<style lang="scss" scoped>
div {
  font-size: 0.2rem;
  padding: 0.1rem 0.3rem;
  input {
    display: block;
    width: 100%;
    height: 0.5rem;
    line-height: 0.5rem;
    margin-bottom: 0.2rem;
    padding: 0 0.1rem;
    border: 1px solid #ccc;
    border-radius: 0.1rem;
  }
  input.confirm {
    margin-bottom: 0;
  }
  input.user{
      margin-top: .3rem;
  }
  a {
    display: inline-block;
    height: 1rem;
    color: #ffd100;
     margin-top: 0.2rem;
  }
  a.home {
    float: left;
  }
  a.login {
    width: auto;
    float: right;
  }
  button {
    font-size: 0.25rem;
    display: block;
    width: 100%;
    height: 0.5rem;
    // background: #ffd100;
    border: none;
    border-radius: 0.1rem;
    color: #666;
  }
  p.tips {
    color: red;
    font-size: 0.15rem;
  }
  p.footer {
    width: 3.15rem;
    text-align: center;
    position: fixed;
    bottom: 0.1rem;
    color: #ffd100;
    font-size: 0.15rem;
  }
}
</style>
